import React from "react";
import {Switch,Route} from "react-router-dom";
import LandingPage from "./compos/LandingPage";
import About from "./compos/About";
import Profile from "./compos/Profile";


const Main=()=>{
	
	return (

			<Switch>
			<Route exact path="/Midget-portfoliio/" component={LandingPage}>
			</Route>
			<Route path="/Midget-portfoliio/about" component={About}/>
			<Route path="/Midget-portfoliio/profile" component={Profile} />
			</Switch>

		);
		
		
}
export default Main;